import * as React from 'react';
import { IBasicButton, IToolbarButtonConfig, IconReduceIndent } from '@ali/4ever-component';
import { PluginRoles } from '@ali/4ever-bamboo';
import { Controller } from '@ali/4ever-cangjie';
import { DECREASE_HOT_KEY_TIP } from '../../utils/hotKeyTips';
import { decreaseIndent } from '../../actions';
;

export interface IDecreaseIndentButtonProps extends IToolbarButtonConfig {
  controller: Controller;
  locale?: Record<string, string>;
}

export default function createDecreaseIndentButton(props: IDecreaseIndentButtonProps): IBasicButton {
  const { controller, locale, icon, title, tooltip, badgeDot, disabled } = props;

  const handleClick = () => controller.run('onAction', decreaseIndent());

  const { isCollapsedAtEdge } = controller.value.selection;
  const isDisabled = disabled || isCollapsedAtEdge || controller.query('isSelectionInHr') || controller.query('isSelectionInListSymbol');

  const buttonConfig: IBasicButton = ({
    icon: icon || <IconReduceIndent />,
    onClick: handleClick,
    title: title || locale?.leftButtonTitle,
    tooltip: {
      title: title || locale?.leftButtonTitle,
      shortCut: DECREASE_HOT_KEY_TIP,
      ...tooltip,
    },
    badgeDot,
    disabled: isDisabled,
    role: PluginRoles.indentDecrease,
    testid: "bi-toolbar-decindent"
  });

  return buttonConfig;

}
